.header {
	min-height: 85px;
	&__in {
		@extend %in;
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		justify-content: space-between;
		align-items: center; }
	&__left {
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		align-items: center;
		width: 100%; }
	&__caption {
		font-size: 24px;
		font-family: 'Ekibastuz-black';
		font-weight: bold; }
	&__text {
		font-family: 'Ekibastuz-bold';
		font-size: 12px;
		color: #656565; }
	&__logo {
		position: relative;
		div {
			text-decoration: underline;
			color: #FFF;
			height: 17px;
			padding: 0 5px;
			background-color: #773d34;
			position: absolute;
			bottom: 0;
			right: 0; } } }

.nav {
	width: 100%;
	text-align: center;
	&__menu {
		li {
			display: inline-block;
			text-decoration: underline;
			margin-right: 20px;
			&:last-child {
				margin-right: 0; }
			a {
				font-size: 16px;
				color: #2056a5;
				&:hover {
					color: #a0392e; } } } } }
.mobile-nav {
	display: none;
	position: relative; }
.mob-menu {
	width: 200px;
	display: none;
	position: absolute;
	background: #fff;
	z-index: 999;
	right: 30px;
	top: 85px;
	padding: 10px 0;
	border: 1px solid #000;
	border-radius: 10px;
	transition: .25s ease-in-out;
	ul {
		li {
			margin-bottom: 5px;
			background-color: #fefefe;
			a {} } } }
.search {
	background-color: #3f3f3f;
	&__in {
		@extend %in;
		min-height: 75px;
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		align-items: center; }
	&__heading {
		white-space: nowrap;
		font-size: 20px;
		color: #FFF;
		margin-right: 17px; }
	&__input {
		width: 100%;
		max-width: 650px;
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		align-items: center;
		position: relative;
		&:after {
			position: absolute;
			right: 15px;
			content: '';
			height: 18px;
			width: 18px;
			background-image: url("../img/search.png"); }
		input {
			width: 100%;
			height: 40px;
			border-left: 1px solid #d0d0d0;
			border-right: 1px solid #d0d0d0;
			border-top: 1px solid #d0d0d0;
			border-radius: 4px;
			padding-left: 20px;
			padding-right: 40px;
			font-size: 15px; }
		/* Поменять */
		&:hover input {
			border-radius: 4px 4px 0 0; } }
	&__list {
		position: absolute;
		display: none;
		width: 100%;
		top: 40px;
		border: 1px solid #dcdcdc;
		border-radius: 0 0 10px 10px;
		top: 40px;
		box-shadow: 0px 7px 15px rgba(0,0,0,0.5);
		background-color: #FFF; }
	&__item {
		width: 100%;
		padding: 0 20px;
		min-height: 60px;
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		justify-content: space-between;
		align-items: center;
		background-color: #FFF;
		border-bottom: 1px solid #dcdcdc;
		&:hover {
			background-color: #eff4fa; } }
	&__left {
		display: flex;
		align-items: center; }
	&__img {
		img {
			height: 32px;
			width: 25px;
			display: block; } }
	&__title {
		margin-left: 16px;
		font-size: 15px; }
	&__right {
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		align-items: center;
		a {
			color: #2e6abb;
			font-size: 15px;
			font-weight: bold;
			&:hover {
				color: #a0392e; } } }
	&__class {
		font-size: 15px;
		font-weight: bold;
		margin-right: 50px; }
	&__coincidences {
		color: #898989;
		min-height: 53px;
		font-size: 15px;
		padding-left: 20px;
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		align-items: center; } }
