@include r(1000) {
	.block8__in {
		padding: 30px 10px!important; } }
@include r(900) {
	.table {
		display: none; }
	.table-mob {
		display: block; } }
@include r(768) {
	.block2__item {
		width: 100%;
		max-width: 100%; }
	.header__in {
		flex-direction: column;
		margin-bottom: 10px; }
	.nav ul li a {
		font-size: 15px; }
	.header__left {
		display: flex;
		order: 2;
		justify-content: space-between; }
	.block7 {
		flex-direction: column; }
	.block9__caption {
		font-size: 27px; }
	.block10 {
		flex-direction: column;
		&__left {
			margin-top: 10px; } }
	.footer {
		margin-top: 50px;
		flex-direction: column;
		align-items: center;
		&__right {
			margin-top: 15px;
			text-align: center; } }
	.nav__menu {
		display: none; }
	.mobile-nav {
		display: block; }
	.nav {
		width: auto; }
	.mob-menu {
		a {
			width: 100%;
			display: block; } }
	.search__in {
		flex-direction: column; }
	.search__item {
		flex-direction: column;
		padding: 5px; }
	.search__right {
		margin-top: 10px; }
	.block6 {
		margin-top: 15px; }
	.block7 {
		margin-top: 20px; }
	.block7__caption, .block8__caption, .block10__caption,.block1__caption {
		text-align: center;
		margin: 0; } }
@include r(450) {
	.block9 {
		margin-top: 30px; }
	.block9__text {
		margin-left: 10px; }
	.block9__caption {
		font-size: 17px; }
	.footer__left {
		flex-direction: column;
		align-items: center; }
	.footer__items {
		width: 100%;
		&:last-child {
			margin-top: 15px; } } }
