}

.breadcrumbs {
  margin-top: 40px;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  align-items: center;
}

.breadcrumbs__item {
  display: flex;
  align-items: center;
}

.breadcrumbs__item:after {
  content: '>';
  color: #000;
  font-size: 15px;
  margin: 0 8px;
  font-weight: bold;
}

.breadcrumbs__item:last-child:after {
  display: none;
}

.breadcrumbs__item a {
  font-size: 15px;
  color: #2e66b3;
}

.breadcrumbs__item a:hover {
  color: #a33b33;
}

.breadcrumbs__item a:visited {
  color: #2e66b3;
}

.breadcrumbs .active a {
  color: #aaa9a9;
  text-decoration: none;
}

