body {
	background: #FFF;
	line-height: 1.4;
	font-family: 'PT Sans', sans-serif;
	font-weight: 400;
	font-size: 12px; }

%in {
	max-width: 980px;
	margin: 0 auto;
	@include r(1000) {
		padding: 0 10px; } }
.content {
	@extend %in; }

.breadcrumbs {
	margin-top: 40px;
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	align-items: center;
	&__item {
		display: flex;
		align-items: center;
		&:after {
			content: '>';
			color: #000;
			font-size: 15px;
			margin: 0 8px;
			font-weight: bold; }
		&:last-child {
			&:after {
				display: none; } }
		a {
			font-size: 15px;
			color: #2e66b3;
			&:hover {
				color: #a33b33; }
			&:visited {
				color: #2e66b3; } } }
	.active {
		a {
			color: #aaa9a9;
			text-decoration: none; } } }



@font-face {
	font-family: 'Ekibastuz';
	src: url('../fonts/Ekibastuz-Regular.otf'); }

@font-face {
	font-family: 'Ekibastuz-black';
	src: url('../fonts/Ekibastuz-Black.otf'); }
@font-face {
	font-family: 'Ekibastuz-bold';
	src: url('../fonts/Ekibastuz-Bold.otf'); }

